import React, { Component } from 'react'
import classes from './Sidebar.module.css';
import user from '../assets/img-user.png';
import smiley from '../assets/img-smiley.png';
import clock from '../assets/img-clock.png';
import desert1 from '../assets/dessert1.jpg'    
import desert2 from '../assets/dessert2.jpg'
export class Sidebar extends Component {
    
    state={
        count: 1,
        itemlTotal:41.48
    }

 

    onclickincrement = ()=>{
        this.setState(prevState => ({
            count: prevState.count + 1
          }),()=>this.calculateTotal());
          
    }
     onclickdecrement = ()=>{
        this.setState(prevState => ({
            count: prevState.count - (prevState.count>=1)? prevState.count-1: prevState.count
          }),()=>this.calculateTotal());
    }

    calculateTotal = () => {
        console.log(this.state.count)
        const itemList = [18.16, 8.6, 14.66];
        const totalcost = itemList.reduce((total, next) => {
            return total  + (this.state.count * next);
        }, 0);
console.log(totalcost)
        this.setState({
            itemlTotal:totalcost

        })

return totalcost
    }
    render() {
        return (
            <div className={classes.Sidebar}>
            <div className={classes.coutwrap}>
            <img src={user} className={classes.user}/>
            <span className={classes.countbg}>3</span>
            </div>
            <div className={classes.order}>
                <div>my <img src={smiley} className={classes.icon}/></div>
                <div>Order</div>
            </div>
            <div className={classes.choosetime_bg}>
                <div className={classes.disp}>
             <div>625 St Marks Ave</div>  
             <div className={classes.edit}>Edit</div> 
             </div>
            <div className={classes.chosetime}>
                <div className={classes.imgflex}><img src={clock}/> 35min</div>
                <div className={classes.edit}>Chose time</div>
            </div>
            </div>
            <div className={classes.orderlist}>
             <span><img src={desert1}/></span>   
             <span>{this.state.count}</span>   
             <span>X</span>   
             <span>Beach <br/>BBQBurger</span>   
             <span className={classes.amount}>{this.state.count * parseFloat(18.16.toFixed(2))}</span>   
            </div>
            
            <div className={classes.orderlist}>
             <span><img src={desert2}/></span>   
             <span>{this.state.count}</span>   
             <span>X</span>   
             <span>french fries</span>   
             <span className={classes.amount}>{this.state.count * parseFloat(8.66.toFixed(2))}</span>   
            </div>
    
            <div className={classes.orderlist}>
             <span><img src={desert1}/></span>   
             <span>{this.state.count}</span>   
             <span>X</span>   
             <span>cheese <br/> sause</span>   
             <span className={classes.amount}>{this.state.count * parseFloat(14.66.toFixed(2))}</span>   
            </div>
    
            <div className={classes.total}>
            <span>Total:</span>    
        <span className={classes.totalbold}>{this.state.itemlTotal}</span>    
            </div>
    
            <div className={classes.checkoutwrap}>
            <div className={classes.person}>
            <h1>person</h1>  
            <div className={classes.inccountwrap}>
        <span onClick={this.onclickdecrement} className={classes.hand}>-</span> <span>
        {this.state.count}</span> <span onClick={this.onclickincrement} className={classes.hand}>+</span></div>  
            </div>    
            <div className={classes.checkout}>Checkout</div>
            </div>
            
            </div>
        )
    }
}

export default Sidebar
