import React from 'react'
import desert1 from '../assets/dessert1.jpg'    
import desert2 from '../assets/dessert2.jpg'    
import desert3 from '../assets/dessert3.jpg'    
import star from '../assets/img-star.png'  
import classes from './Ratings.module.css';
function Ratings() {
    return (
        <div className={classes.SectionWrapper}>
                <div className={classes.desertrow}>
                <div className={classes.dessertcolum}><img src={desert1} className={classes.dessertcolumimg}/>
                <span>25:30</span>
                <div className={classes.subtxt}>Bagel story</div>
                <div className={classes.ratingwrapwr}>
                <div><img src={star}/></div>
                <div>  4.7 Deli bagels $$</div>
                </div>
                </div>    
                <div className={classes.dessertcolum}>
                    <img src={desert2} className={classes.dessertcolumimg}></img>
                <span>30:35</span>
                <div className={classes.subtxt}> The Estaminet</div>
                <div className={classes.ratingwrapwr}>
                <div><img src={star}/></div>
                <div>  4. Cafes Creperies $</div>
                </div>
                </div>    
                <div className={classes.dessertcolum}><img src={desert3} className={classes.dessertcolumimg}/>
                <span>35:40</span>
                <div className={classes.subtxt}>Le paris dakar</div>
                <div className={classes.ratingwrapwr}>
                <div><img src={star}/></div>
                <div>  Creperies sandwich $$</div>
                </div>
                </div> 
                </div>
               </div>
    )
}

export default Ratings
