import React from 'react'
import imgburger from '../assets/img-burger.png';
    import arrdowm from '../assets/img-arr-down.png';
    import allfood from '../assets/img-all-food.png';
    import pizza from '../assets/img-pizza.png';
    import asians from '../assets/img-asians.png';
    import burgers from '../assets/img-burgers.png';
    import barbeque from '../assets/img-barbeque.png';
    import dessert from '../assets/img-dessert.png';
    import thai from '../assets/img-thai.png';
    import sushi from '../assets/img-sushi.png';
    import arright from '../assets/img-arr-right.png';
    import classes from './Slider.module.css';
function Slider() {
    return (
        <div className={classes.SectionWrapper}>
            <div className={classes.MenuSection}>
                <div className={classes.headerrow}>
                <div><span>Restaurants</span><img src={imgburger} className={classes.imgburger} /></div>
                <div>
                
                <div class="select">
    <select name="format" id="format">
        <option selected disabled>Delivery:now</option>
        <option value="pdf">PDF</option>
        <option value="txt">txt</option>
        <option value="epub">ePub</option>
        <option value="fb2">fb2</option>
        <option value="mobi">mobi</option>
        <img src={arrdowm} className={classes.arrdown} alt=''/>
    </select>
    
    </div>
    </div>
                </div>
               <div className={classes.slide}>
                <span>
                <div className={classes.menuiconbg}>
                    <img src={allfood}/>
                    </div>
                    <p>All</p>
                </span>   
                <span>
                <div className={classes.menuiconbg}>
                    <img src={pizza}/>
                    </div>
                    <p>Pizza</p>
                </span>   
                <span>
                <div className={classes.menuiconbg}>
                    <img src={asians}/>
                    </div>
                    <p>Asian</p>
                </span>   
                <span>
                <div className={classes.menuiconbg}>
                    <img src={burgers}/>
                    </div>
                    <p>Burgers</p>    
                </span>   
                <span>
                <div className={classes.menuiconbg}>
                    <img src={barbeque}/>
                    </div>
                    <p>Barbeque</p>     
                </span>   
                <span>
                <div className={classes.menuiconbg}>
                    <img src={dessert}/>
                    </div>
                    <p>Dessers</p> 
                </span>
                <span>
                <div className={classes.menuiconbg}>
                    <img src={thai} className={classes.pip}/>
                    </div>
                    <p>Thai</p> 
                </span>   
                <span>
                <div className={classes.menuiconbg}>
                    <img src={sushi}/>
                    </div>
                    <p>Sushi</p>    
                </span>
                  <a className={classes.arrow}><img src={arright}/></a>
               </div>
                </div>
                </div>
        
    )
}

export default Slider
